<template>
	<view>
        <view class="logo">
			<view class="img">
				<image mode="widthFix" src="data:image/png;base64,"></image>
			</view>
		</view>
		<view class="form re">
			<view class="code">
				<input placeholder="请输手机号" maxlength="12" v-model="rePwd.telephone" placeholder-style="color: rgba(255,255,255,0.8);"/>
			</view>
			<view class="username">
				<view class="get-code" :style="{'color':getCodeBtnColor}" @click.stop="getCode()">{{getCodeText}}</view>
				<input placeholder="验证码" maxlength="6" v-model="code" placeholder-style="color: rgba(255,255,255,0.8);"/>
			</view>
			<view class="password">
				<input placeholder="请输入密码" maxlength="20" v-model="rePwd.password" password=true placeholder-style="color: rgba(255,255,255,0.8);"/>
			</view>
			<view class="btn" @tap="doReset">重置密码</view>

		</view>
		
	</view>
</template>

<script>
	import md5 from "@/common/SDK/md5.min.js";
	export default {
		data() {
			return {
				rePwd:{
					telephone:'',
					password:'',
					authCode:''
				},
				code:'',
				passwd:"",
				getCodeText:'获取验证码',
				getCodeBtnColor:"#ffffff",
				getCodeisWaiting:false
			}
		},
		onLoad() {

		},
		methods: {
			Timer(){},
			getCode(){
				const _that = this
				if(this.getCodeisWaiting){
					return ;
				}
				if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(_that.rePwd.telephone))){ 
					uni.showToast({title: '请填写正确手机号码',icon:"none"});
					return false; 
				} 
				this.getCodeText = "发送中..."
				this.getCodeisWaiting = true;
				this.getCodeBtnColor = "rgba(255,255,255,0.5)"
				//示例用定时器模拟请求效果
				setTimeout(()=>{
					uni.showToast({title: '验证码已发送',icon:"none"});
					uni.request({
						url:_that.$serverUrl + '/sso/getAuthCode?telephone=' + this.rePwd.telephone,
						// url:'http://192.168.1.105:8201/mall-portal/sso/getAuthCode?telephone=' + this.rePwd.telephone,
						method:'GET',
						success(res) {
							_that.rePwd.authCode = res.data.data
							console.log(res)
						}
					})
					this.setTimer();
				},1000)	
			},
			setTimer(){
				let holdTime = 60;
				this.getCodeText = "重新获取(60)"
				this.Timer = setInterval(()=>{
					if(holdTime<=0){
						this.getCodeisWaiting = false;
						this.getCodeBtnColor = "#ffffff";
						this.getCodeText = "获取验证码"
						clearInterval(this.Timer);
						return ;
					}
					this.getCodeText = "重新获取("+holdTime+")"
					holdTime--;
					
				},1000)
			},
			doReset(){
				const _that = this
				uni.hideKeyboard()
				//模板示例部分验证规则
				if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.rePwd.telephone))){ 
					uni.showToast({title: '请填写正确手机号码',icon:"none"});
					return false; 
				} 
				//示例验证码，实际使用中应为请求服务器比对验证码是否正确。
				if(this.code !== this.rePwd.authCode){ 
					uni.showToast({title: '验证码不正确',icon:"none"});
					this.code = ''
					return false; 
				}
				if(this.rePwd.password.length < 6){ 
					uni.showToast({title: '密码不可少于6位',icon:"none"});
					this.rePwd.password = ''
					return false; 
				}
				uni.showLoading({
					title: '提交中...'
				})
				//模板示例修改本地储存的用户信息，实际使用中请替换为上传服务器修改。
				setTimeout(()=>{
					uni.request({
						url:_that.$serverUrl + '/sso/updatePassword?telephone='+_that.rePwd.telephone+'&password='+_that.rePwd.password+'&authCode='+_that.rePwd.authCode,
						method:'POST',
						success(res) {
							if(res.data.code === 200){
								uni.hideLoading()
								uni.showToast({
									title:res.data.message+'正在为您跳转至登陆页面',
									icon:'none'
								})
								setTimeout(() => {
									uni.navigateTo({
										url:'./login'
									})
								},1000)
							}
						},
						fail(res2) {
							uni.hideLoading()
							uni.showToast({
								title:'网络开小差了，请稍后重试'
							})
						}
					})
				},1000)
			}
		}
	}
</script>

<style lang="scss">
	@import "../../static/css/login.scss";

	
</style>
